var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("卢克·天行者")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        卢克·天行者是一个虚构的人物和主要主角的的原电影三部曲中的星球大战通过建立专营乔治·卢卡斯。天马行者由马克·哈米尔（Mark Hamill）描绘，最初出现在1977年的原版电影中，并在“帝国反击战”（1980年）和“绝地归来”（1983年）中回归。三十年后，他描绘了星球大战续集三部曲中的角色，从2015年的The Force Awakens和2017 年的The Last Jedi开始.Hamill将在即将上映的电影中重演他的角色，天行者的崛起（2019）。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        卢克是在一个举足轻重的人物反抗军的对抗斗争银河帝国，一个朋友和最终妹夫走私的汉·索罗和未知的他，直到绝地归来，叛乱领导人的孪生兄弟莱娅公主。他在Jedi Masters Obi-Wan Kenobi和Yoda的训练下，是Anakin Skywalker和PadméAmidala的儿子。他指导Rey，续集三部曲的主角，并且是续集三部曲的对手Kylo Ren的母亲叔叔。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在拍摄原版“星球大战”三部曲的过程中，乔治·卢卡斯告诉哈米尔，他需要在中年时重新扮演这个角色。卢卡斯进一步解释说，他作为卢克的角色将把“ 神剑传给下一代”。哈米尔当时没有反应，因为他想将未来几十年的星球大战电影作为一个抽象的概念。2012年夏天，当卢卡斯告诉他一个正在开发中的时候，哈米尔在星球大战庆典上了解了续集三部曲。哈米尔回忆剃胡须描绘诈欺在闪光，然后让它重新生长为拍摄星球大战。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        由于编剧迈克尔·阿恩特担心他的存在意味着观众对主角雷伊不太感兴趣，导致达成一项协议，即他将从屏幕上删除而成为情节设备，因此卢克在The Force Awakens中缺少屏幕时间。哈米尔出席了剧本阅读会议，并帮助隐瞒卢克在电影中的角色; 而不是对话，他阅读了舞台指示。艾布拉姆斯说，这让他能够继续参与，而他的阅读有助于为每个人提供“更好的体验”。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
